<template>
  <div id="app" class="dt-widget">
    <transition name="fade">
      <Loading v-if="showLoading"></Loading>
    </transition>
    <Calculator v-if="userStep === 'main'" @readyToSubmit="checkPhone($event)"></Calculator>
    <Registration v-if="userStep === 'registration'"
                  :phone="calculatorData.phoneNumber"
                  @sentRegistrationData="sentRegistrationData($event)">

    </Registration>
    <Verify v-if="userStep === 'verify'"
            :phone="registrationData.phone"
            :email="registrationData.email">
    </Verify>
  </div>
</template>

<script>
import Calculator from './components/Calculator/Calculator';
import Registration from './components/Registaration/Registration';
import Verify from './components/Verify/Verify';
import Loading from './components/Loading/Loading';

export default {
  name: 'app',
    data() {
      return {
          userStep: 'main',
          calculatorData: {},
          registrationData: {},
          showLoading: false,
      }
    },
  components: {
      Calculator,
      Registration,
      Verify,
      Loading,
  },
  methods: {
      checkPhone(data) {
          this.showLoading = true;
          setTimeout(() => {
              this.showLoading = false;
              this.calculatorData = data;
              this.userStep = 'registration';
          }, 2000);
      },
      sentRegistrationData(data) {
          this.showLoading = true;
          setTimeout(() => {
              this.showLoading = false;
              this.registrationData = data;
              this.userStep = 'verify';
          }, 2000);
      },
  },
}
</script>
<style src="./ifinance.css"></style>
